import { postRequest} from './commonApi'
import { endPoints } from './endPoints'

const useApiSaveResponse = () => {
  const apiSaveResponse = async (data, json, header) => {
    const url = endPoints.saveEmailResponse

    try {
      let res = await postRequest(url, data, json = true, header = {key: process.env.REACT_APP_OLIV_PUBLIC_API_KEY})

      if (res.success) {
        return res
      }
    } catch (err) {
      console.log('ERROR', err)
    }
  }

  return { apiSaveResponse }
}

export default useApiSaveResponse



