import React from 'react'
import { withRouter, Link } from 'react-router-dom'
import { notFoundRedirects } from 'Shared/constants/Routes'
import { Helmet } from 'react-helmet'
import { PrimaryButton } from 'Shared/components/Button'
import withTranslations from 'Shared/hoc/withTranslations'
import 'Shared/layouts/AppLayout/AppLayout.scss'
import './TrackRsponse.scss'
import { useEffect } from 'react'
import useApiSaveResponse from '../../services/api/useApiSaveResponse'

const TrackRsponse = (props) => {
  const { t, userRole } = props
  const pathnameValues = props.location.pathname.split('/')
  const getUrlData = {'key': pathnameValues[2], 'email': pathnameValues[3], 'status': pathnameValues[4]}
  const hasEmailResponse = process.env.REACT_APP_FEATURE_FLAG_EMAIL_RESPONSE
  const {apiSaveResponse} = useApiSaveResponse()

  console.log('GET INFO...', getUrlData)

  useEffect(() => {
    const onSaveResponse = async () => {
      const resp = await apiSaveResponse({ key: getUrlData.key, email: getUrlData.email, response: getUrlData.status })
      try {
        if (resp) {
          // handleForm()
          console.log('success.........')
        }
      } catch (error) {
        console.log('ERROR', error)
        console.log('false error.........')
      } finally {
        // setIsDisable(false)
        console.log('false.........')
      }
    }

    onSaveResponse()
  }, [])

  return (
    <div className='not-found__container parsing--hidden'>
      <Helmet>
        <meta name='status' content={410} />
      </Helmet>
      <div className='not-found__container-table'>
        <div className='not-found__table-cell'>
          {/* <h1 className='not-found__header'>{t('notFoundPage.header')}</h1> */}
          <h1 className='not-found__header'>Thankyou! Your response has been recorded.</h1>
          <p className='not-found__description'>Reach out to <a href='mailto:team@oliv.com'>team@oliv.com</a> for any queries, we're here to help.</p>
          {/* <div className='info_user'>
            <span><b>Email:</b> {getUrlData.email}</span>
            <span><b>Status:</b> {getUrlData.status}</span>
          </div> */}
          <Link to={notFoundRedirects[userRole]}>
            <PrimaryButton>{t(`notFoundPage.ctaButton.${userRole}`)}</PrimaryButton>
          </Link>
        </div>
      </div>
    </div>
  )
}

export default withRouter(withTranslations(TrackRsponse))



